import React from 'react';
import '../Styles/SectionPage.css';

function SectionPage({ title, description, imageUrl, items = [], onBusinessClick }) {
  return (
    <div className="section-page">
      <div className="section-header" style={{ backgroundImage: `url(${imageUrl})` }}>
        <h1>{title}</h1>
        <p className="section-description">{description}</p>
      </div>
      <div className="section-content">
        {items.map((item) => (
          <div 
            key={item.id} 
            className="business-item" 
            onClick={() => onBusinessClick(item.id)} // Use the click handler from props
          >
            <h2>{item.name}</h2>
            <p>{item.description}</p>
          </div>
        ))}
      </div>
    </div>
  );
}

export default SectionPage;